<div class="payments-block"><div class="payments-block__title title">Выплата фиксированного ежегодного дохода</div><div class="payout-info"><div class="payout-info__content"><div class="payout-info__list"><div class="payout-info__item" v-for="(item, idx) of time"><div class="payout-info__item-fee"><span class="payout-info__item-fee--text">{{ idx + 1 }}-й взнос</span><span class="payout-info__price">{{ sum < 100000 ? addSpacesOnInput(100000) :  addSpacesOnInput(sum)}}</span></div><div class="payout-info__item-pay"><div class="payout-info__block-text"><span class="payout-info__text">выплата</span><span class="payout-info__percent">{{payoutPercent(idx + 1)}}</span></div><span class="payout-info__price">{{ payoutSum(idx + 1) }} ₽</span></div></div></div><div class="payout-info__result"><div class="payout-info__result-img"></div><div class="payout-info__result-info"><span class="payout-info__result-text">все взносы</span><span class="payout-info__price payout-info__price_total">+ {{ totalSum }}</span></div></div></div></div></div>