<div class="calculator"><div class="calculator--form"><h4 class="calculator--form__title title">Ежегодный взнос</h4><input id="range-input" v-model="sum" maxLength="8" @focus="clearRubles" @blur="addRubles" @input="change($event.target.value)"><div class="slider-input__terms"><div class="slider-input__terms-text-l" :class="{'sub-title__error': error}">{{ minValue }}</div><div class="slider-input__terms-text-r" :class="{'sub-title__error': error}">{{ maxValue }}</div></div></div><a><button class="btn payments-block__btn" v-if="width &gt; 959" :disabled="disabled" @click="orderUrl" :class="{'disabled': disabled}">Оформить</button></a></div>